import React from 'react';
import '../App.css'; // CSS
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import yup from './ContactMe.jp.js';
import { TextField, Typography } from '@mui/material'; // MUI
// import emailjs from 'emailjs-com'; // EmailJS

const schema = yup.object({
  // 
  name: yup
    .string()
    .label('')
    .trim().lowercase()
    .transform((value, orgValue) => value.normalize('NFKC')) // 
    .required()
    .max(30),
  email: yup
    .string()
    .label('')
    .required()
    .email(),
  comment: yup
    .string()
    .label('')
    .required()
    .test('ng',
      ({ label }) => `${label}NG`,
      value => {
        const ngs = ['', '', ''];
        for (const ng of ngs) {
          if (value.includes(ng)) {
            return false;
          }
        }
        return true;
      })
});

export const ContactMe = () => {
  const { register, handleSubmit, reset, formState: { errors } } = useForm({
    defaultValues: {
      name: '',
      email: '',
      comment: ''
    },
    // Yup
    resolver: yupResolver(schema),
  });

  // 
  const onsubmit = data => {
      // 
      console.log(data); // 
      alert(''); // 
      reset(); // 
    // EmailJS
    // emailjs.send('YOUR_SERVICE_ID', 'YOUR_TEMPLATE_ID', data, 'YOUR_USER_ID')
    //   .then((response) => {
    //     console.log('', response.status, response.text);
    //     alert(''); // 
    //     reset(); // 
    //   }, (err) => {
    //     console.error('', err);
    //     alert(''); // 
    //   });
  };

  // 
  const onerror = err => {
    console.error(err);
    alert(''); // 
  };

  return (
    <div className="main" id="contact">
      <div className="overlay-main">
        <div className="center">
          <Typography variant="h2" className="title-text">Contact Me</Typography>
          <div className="text">
            <p></p>
            <p></p>
            <p></p>
          </div>
          <div className="styled-body">
            <form onSubmit={handleSubmit(onsubmit, onerror)} noValidate className="styled-form">
              <div className="form-group">
                <TextField
                  label=""
                  variant="outlined"
                  margin="normal"
                  fullWidth
                  {...register('name')} // name
                  error={!!errors.name} // true
                  helperText={errors.name?.message} // 
                  InputProps={{
                    style: { backgroundColor: 'white' }, // 
                  }}
                />
              </div>
              <div className="form-group">
                <TextField
                  label=""
                  type="email"
                  variant="outlined"
                  margin="normal"
                  fullWidth
                  {...register('email')} // email
                  error={!!errors.email} // true
                  helperText={errors.email?.message} // 
                  InputProps={{
                    style: { backgroundColor: 'white' } // 
                  }}
                />
              </div>
              <div className="form-group">
                <TextField
                  label=""
                  variant="outlined"
                  margin="normal"
                  fullWidth
                  multiline
                  rows={4} // 
                  {...register('comment')} // comment
                  error={!!errors.comment} // true
                  helperText={errors.comment?.message} // 
                  InputProps={{
                    style: { backgroundColor: 'white' } // 
                  }}
                />
              </div>
              <div className="submit-container">
                <button type="submit" className="submit-button"></button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ContactMe;
