import { useState } from 'react';

/**
 * useModal 
 * 
 * 
 * @returns {Object} 
 */
const useModal = () => {
  // 
  const [open, setOpen] = useState(false);
  
  // 
  const [content, setContent] = useState(null);

  /**
   * 
   * @param {ReactNode} content - 
   */
  const handleOpen = (content) => {
    setContent(content); // 
    setOpen(true);      // 
  };

  /**
   * 
   */
  const handleClose = () => {
    setOpen(false);     // 
    setContent(null);   // 
  };

  // 
  return {
    open,          // 
    content,       // 
    handleOpen,    // 
    handleClose,   // 
  };
};

export default useModal;
